
@font-face {
  font-family: 'Geometria-Regular';
  src: url('../fonts/Geometria/Geometria-Regular/Geometria-Regular.eot');
  src: local('Geometria Regular'), local('Geometria-Regular'),
      url('../fonts/Geometria/Geometria-Regular/Geometria-Regular.eot?#iefix') format('embedded-opentype'),
      url('../fonts/Geometria/Geometria-Regular/Geometria-Regular.woff2') format('woff2'),
      url('../fonts/Geometria/Geometria-Regular/Geometria-Regular.woff') format('woff'),
      url('../fonts/Geometria/Geometria-Regular/Geometria-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Geometria-Light';
  src: url('../fonts/Geometria/Geometria-Light/Geometria-Light.eot');
  src: local('Geometria Light'), local('Geometria-Light'),
      url('../fonts/Geometria/Geometria-Light/Geometria-Light.eot?#iefix') format('embedded-opentype'),
      url('../fonts/Geometria/Geometria-Light/Geometria-Light.woff2') format('woff2'),
      url('../fonts/Geometria/Geometria-Light/Geometria-Light.woff') format('woff'),
      url('../fonts/Geometria/Geometria-Light/Geometria-Light.ttf') format('truetype');
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: "MyriadPro-Regular";
  src: url("../fonts/MariadPro/MariadPro-Regular/MyriadPro.eot"); /* IE9 Compat Modes */
  src: url("../fonts/MariadPro/MariadPro-Regular/MyriadPro.eot?#iefix") format("embedded-opentype"), /* IE6-IE8 */
    url("../fonts/MariadPro/MariadPro-Regular/MyriadPro.svg") format("svg"), /* Legacy iOS */
    url("../fonts/MariadPro/MariadPro-Regular/MyriadPro.woff") format("woff"), /* Modern Browsers */
    url("../fonts/MariadPro/MariadPro-Regular/MyriadPro.woff2") format("woff2"); /* Modern Browsers */
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "MyriadPro-Bold";
  src: url("../fonts/MariadPro/MyriadPro-Bold/MyriadPro-Bold.eot"); /* IE9 Compat Modes */
  src: url("../fonts/MariadPro/MyriadPro-Bold/MyriadPro-Bold.eot?#iefix") format("embedded-opentype"), /* IE6-IE8 */
    url("../fonts/MariadPro/MyriadPro-Bold/MyriadPro-Bold.otf") format("opentype"), /* Open Type Font */
    url("../fonts/MariadPro/MyriadPro-Bold/MyriadPro-Bold.svg") format("svg"), /* Legacy iOS */
    url("../fonts/MariadPro/MyriadPro-Bold/MyriadPro-Bold.ttf") format("truetype"), /* Safari, Android, iOS */
    url("../fonts/MariadPro/MyriadPro-Bold/MyriadPro-Bold.woff") format("woff"), /* Modern Browsers */
    url("../fonts/MariadPro/MyriadPro-Bold/MyriadPro-Bold.woff2") format("woff2"); /* Modern Browsers */
  font-weight: normal;
  font-style: normal;
}