.posters {
    padding-top: 130px;
    color: #000;
    z-index: 10;
    position: relative;
    background-color: #fff;
    @include lg(padding-top, 50px);
    &__container {
        @include container();
    }
    &__section {
        h1 {
            text-align: center;
            font-family: $mb;
            font-size: 38px;
            line-height: 45px;
            text-transform: uppercase;
            margin-bottom: 50px;
            @include md(font-size, 24px);
            @include md(line-height, 24px);
            @include sm(font-size, 18px);
        }
    }
    &__article {
        display: flex;
        height: 520px;
        margin-bottom: 110px;
        @include lg(flex-direction, column);
        @include lg(height, auto);
        &-right {
            flex-direction: row-reverse;
            p {
                text-align: right;
                float: right;
                @include lg( text-align, center);
            }
            a {
                float: right;
                @include lg( text-align, center);
            }
        }
    }
    &__description {
        @include size(4);
        @include size-lg(12);
        position: relative;
        @include lg(width, 100%);
        @include lg(text-align, center);
        p {
            width: 300px;
            font-size: 24px;
            font-family: $gl;
            margin-top: 50px;
            @include lg(width, 100%);
        }
    }
    &__image {
        @include size(8);
        @include size-lg(12);
        @include lg(width, 100%);
        img {
            width: 100%;
            height: 100%;
        }
    }
}

.text-effect {
    margin-top: 50px;
    @include md(margin-top,  0);
    
    h2 {
        color: #454545;
        background: transparent;
        border-width: 2px;
        border-style: solid;
        border-color: #454545;
        position: relative;
        display: inline-block;
        padding: 25px 20px;
        transition: all 0.3s ease-in-out;
        text-align: center;
        font-family: $mb;
        font-weight: bold;
        text-transform: uppercase;
        font-size: 36px;
        width: 420px;
        @include sm(width, 280px);
        @include sm(font-size, 18px);
        @include sm(padding, 20px 0);
    }
    h2:before,
    h2:after {
        content: "";
        display: block;
        position: absolute;
        border-color: #454545;
        box-sizing: border-box;
        border-style: solid;
        width: 1em;
        height: 1em;
        transition: all 0.3s ease-in-out;
    }
    h2:before {
        top: -6px;
        left: -6px;
        border-width: 2px 0 0 2px;
        z-index: 5;
    }
    h2:after {
        bottom: -6px;
        right: -6px;
        border-width: 0 2px 2px 0;
    }
    h2:hover:before,
    h2:hover:after {
        width: calc(100% + 12px);
        height: calc(100% + 12px);
        border-color: #fff;
    }
    h2:hover {
        color: #353535;
        background-color: #fff;
        border-color: #fff;
    }
}
.text-effect_right  {
    float: right;
    display: grid;
    justify-content: flex-end;
    @include lg(justify-content, center);
    @include lg(float, none);
}
.text-effect_right h2 {
    display: flex;
    justify-content: flex-end;
    @include lg(justify-content, center);
}
.text-effect_blue h2 {
    // color: #3bb4e5;
    border-color: #3bb4e5;
}
.text-effect_blue h2:before,
.text-effect_blue h2:after {
    border-color: #3bb4e5;
}
.text-effect_blue h2:hover:before,
.text-effect_blue h2:hover:after {
    width: calc(100% + 12px);
    height: calc(100% + 12px);
    border-color: #3bb4e5;
    transform: rotateY(180deg);
}
.text-effect_blue h2:hover {
    color: #3bb4e5;
    background-color: transparent;
    border-color: #3bb4e5;
}
.text-effect_blue .text-effect {
    max-width: 960px;
    text-align: center;
    position: relative;
    margin: auto;
}

.button {
    float: left;
    min-width: 150px;
    max-width: 250px;
    display: block;
    margin: 1em;
    padding: 1em 2em;
    border: none;
    background: none;
    color: inherit;
    vertical-align: middle;
    position: relative;
    z-index: 1;
    -webkit-backface-visibility: hidden;
    -moz-osx-font-smoothing: grayscale;
    
}
.button.button--aylen {
    background: $blue;
    color: #37474f;
    overflow: hidden;
    -webkit-transition: color 0.3s;
    transition: color 0.3s;
    text-align: center;
    font-family: $mb;
    margin-top: 100px;
    @include lg(float, right);
    @include lg(margin-top, 50px);
}
// .button--border-thin {
//     border: 1px solid;
// }
.button--round-s {
    border-radius: 1px;
}
.button--aylen:hover {
    color: #fff;
}
.button--aylen::before, .button--aylen::after {
    content: '';
    position: absolute;
    height: 100%;
    width: 100%;
    bottom: 100%;
    left: 0;
    z-index: -1;
    -webkit-transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    -webkit-transition-timing-function: cubic-bezier(0.75, 0, 0.125, 1);
    transition-timing-function: cubic-bezier(0.75, 0, 0.125, 1);
}
.button--aylen::before {
    background: #7986cb;
}
.button--aylen:hover::before, .button--aylen:hover::after {
    -webkit-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
}
.button--aylen:hover::after {
    -webkit-transition-delay: 0.175s;
    transition-delay: 0.175s;
}
.button--aylen::after {
    background: #3f51b5;
}